import imagePreview from "../public/imagePreview.vue";
import ImageUpload from "../public/imageUpload.vue";
export default {
  components: { imagePreview, ImageUpload },
  data() {
    return {
      text: "",
    };
  },
  watch: {
    processes(newValue) {
      console.log(newValue)
      newValue.map((value, index) => {
        value.order = `md:order-${(value.id % 2) + 1}`;
        console.log(value);
      });
    },
  },
  computed: {
    processes() {
      return this.$store.getters.method_preview;
    },
    lan() {
      return this.$store.getters.lan;
    },
  },
  methods: {
    processUpdate(index) {
      console.log(index)
      let newData = "";
      let data = JSON.parse(JSON.stringify(this.processes));
      console.log(data);
      for (let val of data) {
        if (val.id == index) {
          val.text = event.target.value;
          newData += `${val.text}<###**%%###>${val.img}<###**^^###>`;
        }else {
          newData += `${val.text}<###**%%###>${val.img}<###**^^###>`;
        }
      }
      console.log(newData);
      this.$store.commit("foodMethods_temp_Edit", newData);
    },
    newImage(url, index) {
      let newData = "";
      let data = JSON.parse(JSON.stringify(this.processes));
      console.log(data);
      for (let val of data) {
        if (val.id == index) {
          val.img = url;
          newData += `${val.text}<###**%%###>${val.img}<###**^^###>`;
        }
      }
      console.log(newData);
      this.$store.commit("foodMethods_temp_Edit", newData);
    },
    delete_res(index) {
      let newData = "";
      let data = JSON.parse(JSON.stringify(this.processes));
      console.log(data);
      for (let val of data) {
        if (val.id == index) {
          val.img = "";
          newData += `${val.text}<###**%%###>${val.img}<###**^^###>`;
        }
      }
      console.log(newData);
      this.$store.commit("foodMethods_temp_Edit", newData);
    },
    delete_step(index) {
      console.log(index);
      let newData = "";
      let data = JSON.parse(JSON.stringify(this.processes));
      console.log(data);
      for (let val of data) {
        if (val.id != index) {
          newData += `${val.text}<###**%%###>${val.img}<###**^^###>`;
        }
      }
      console.log(newData);
      this.$store.commit("foodMethods_temp_Edit", newData);
    },
  },
};
